import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'


import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { faBullhorn, faMap, faQuestion, faCreditCard, faUserCircle, faPaperPlane, faHeadset } from '@fortawesome/free-solid-svg-icons'
import { faRedhat, faTwitter, faLinkedinIn ,faYoutube, faDiscord } from '@fortawesome/free-brands-svg-icons'

axios.defaults.baseURL = 'http://localhost:8080'

library.add(faBullhorn, faMap, faQuestion, faCreditCard, faRedhat, faUserCircle, faTwitter, faLinkedinIn, faPaperPlane, faYoutube, faDiscord, faHeadset)
createApp(App).component('font-awesome-icon', FontAwesomeIcon).use(store).use(router, axios).mount('#app')