<template>
  <header class="header black">
      <div class="header navbar-brand">
          <router-link to="/">
              <img class="site-logo" alt="HiH logo" src="../assets/logo.png">
          </router-link>
          <strong class="site-name navbar-end">HACKERS IMPROVE HERE!</strong>
          <!-- <div :class="{'hide-item': !isDesktop }" class="buttons navbar-end">
              <router-link to="/signup" class="button is-success"><strong>Sign up</strong></router-link>
              <router-link to="/login" class="button is-light"><strong>Login</strong></router-link>
          </div> -->
      </div>
      <nav :class="{'dark': isDesktop }"> 
      <div :class="{ 'is-transformed': !showNavbar }">
          <div class="navbar-burger-menu">
              <a :aria-expanded="isActive" :class="{ 'is-active black': isActive }" role="button" class="navbar-burger"
                  aria-label="menu" data-target="collapse" @click="isActive = !isActive">
                  <span aria-hidden="true" />
                  <span aria-hidden="true" />
                  <span aria-hidden="true" />
              </a>
          </div>
          <div id="collapse" :class="{ 'is-active dark': isActive, 'center': isDesktop }" class="navbar-menu is-paddingless">
                  <router-link to="/" class="menu-item">Home</router-link>
                  <router-link to="/learning-paths" class="menu-item">Learning Paths</router-link>
                  <router-link to="/ctf" class="menu-item">Capture The Flag</router-link>
                  <router-link to="/events" class="menu-item">News & Events</router-link>
                  <router-link to="/about-us" class="menu-item">About Us</router-link>
                  <router-link to="/contact-us" class="menu-item">Contact</router-link>
                  <!-- <router-link to="/login" :class="{'hide-item': isDesktop }" class="menu-item" >Login</router-link>
                  <router-link to="/signup" :class="{'hide-item': isDesktop }" class="menu-item">Sign up</router-link> -->
              </div>
          </div>
      </nav>
  </header>
</template>

<script>
import throttle from 'lodash/throttle'

export default {
  name: 'Header',
  data() {
      return {
          isDesktop: false,
          isActive: false,
          showNavbar: true,
          lastScrollPosition: 0
      }
  },
  mounted() {
      this.$nextTick(() => {
          this.checkScreenSize(),
          window.addEventListener('resize', throttle(this.closeMenu, 500))
          window.addEventListener('scroll', throttle(this.hideNav, 250))
      })
  },
  beforeDestroy() {
      window.removeEventListener('resize', this.closeMenu)
      window.removeEventListener('scroll', this.hideNav)
  },
  methods: {
      checkScreenSize() {
      this.isDesktop = window.innerWidth > 1023
      },
      closeMenu() {
          this.isActive = false,
          this.checkScreenSize()
      },
      hideNav() {
          const currentScrollPosition =
              window.pageYOffset || document.documentElement.scrollTop
          if (currentScrollPosition < 0) return
          if (Math.abs(currentScrollPosition - this.lastScrollPosition) < 60) return
          this.showNavbar = currentScrollPosition < this.lastScrollPosition
          this.lastScrollPosition = currentScrollPosition
          setTimeout(this.closeMenu, 250)
      }

  }
}
</script>

<style lang="scss">
  .menu-item.hide-item {
    display: none;
  }
  div.hide-item {
    display: none;
  }
</style>